<template>
  <v-app>
    <v-app-bar app class="high-zindex toolbar-color" dark>
      <div class="d-flex align-center">
        <a href="https://versobyte.com" class="brand">Versobyte</a>
      </div>
    </v-app-bar>

    <v-content>
      <router-view></router-view>

      <v-dialog v-model="block" persistent>
        <v-card>
          <v-card-title class="headline">Disable your ad-blocker</v-card-title>

          <v-card-text>
            <strong>Please disable your ad-blocker, we rely on the widget provided by Arc.io to support COVID-19 response fund.</strong>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>

            <v-btn color="primary" text onclick="window.location.reload(false); ">Oh, ok</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-content>

    <v-footer height="auto" class="ft-footer">
      <v-layout justify-center row wrap>
        <v-flex
          primary
          lighten-2
          py-3
          text-xs-center
          white--text
          xs12
          class="footer"
        >Copyright &copy; Versobyte, All Rights Reserved.</v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  name: "App",

  components: {},

  data: () => ({
    block: false,
  }),

  watch: {
    $route(to) {
      document.title = to.meta.title || "Versobyte";
    }
  },

  mounted: function() {
    
    document.title = "Versobyte";
    var vthis = this
    setTimeout(function () {
      if (!document.getElementById("arc-widget-container")) {
        vthis.block = true;
      }
    }, 2000);
  }
};
</script>

